<template>
  <div class="hello">
    <h1>自定义</h1>
    <input v-model="dataResult">

  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      dataResult:[],
    }

  },

  created(){
    this.$http.get("/").then(res=>{        
        this.dataResult=res.data;
        console.log(this.dataResult)
        }) 



  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



