<template>
  <div>
    <h1>生成三题</h1>
    <el-container>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span v-text="novelType"></span>
          <span>小说</span>
          <el-button
            style="float: right; padding: 3px 0"
            type="text"
            @click="refresh"
            >刷新</el-button
          >
        </div>
        <div v-for="o in topics" :key="o" class="text item">
          {{ o }}
        </div>
      </el-card>
    </el-container>
    <el-container>
      <el-input
        type="textarea"
        placeholder="请输入内容"
        v-model="textarea"
        maxlength="300"
        show-word-limit
      >
      </el-input>
    </el-container>
    <el-container>
      <el-button @click="printStart">打印演示</el-button>
    </el-container>
    <el-row>
      <el-col :xs="24" :sm="14" :md="4" :lg="4" :xl="4">
        
          <!-- 打印效果 -->

          <printText :text="textPrint"> </printText>
        
      </el-col>
    </el-row>
  </div>
</template>

<script>
import printText from "./printText.vue";
export default {
  name: "generateTopic",
  components: { printText },
  data() {
    return {
      topics: [],
      novelType: [],
      textarea: "",
      textPrint: "",
    };
  },
  created() {
    this.genTopic();
    this.genNovelType();
  },
  methods: {
    //生成三题
    genTopic() {
      //生成随机数

      let itemCount = this.global.topicItems.length;
      let i = 0;
      let arr = [];
      let newItem = [];
      while (arr.length < 3) {
        let num = parseInt(Math.random() * itemCount);
        console.log(num);
        // parseInt取正，小数点后面的数字全部抹掉
        // Math.random() 0-1的随机数
        if (arr.indexOf(num) == -1) {
          // this.arr.indexOf(num)若等于-1则证明arr这个数组里没有num这个随机数，因此可以放进这个数组里
          arr.push(num);
          newItem[i] = this.global.topicItems[num];
          i = i + 1;
        }
      }
      this.topics = newItem;

      // console.log(this.topics);
      // console.log(arr);
    },
    //生成小说类别
    genNovelType() {
      let itemCount = this.global.novelType.length;
      let num = parseInt(Math.random() * itemCount);
      this.novelType = this.global.novelType[num];
      console.log(this.novelType);
    },
    refresh() {
      this.genTopic();
      this.genNovelType();
    },
    printStart() {
      this.textPrint = this.textarea;
      console.log(this.textPrint);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 480px;
}
</style>



