import Vue from 'vue'
import Router from 'vue-router'
import generateTopic from '@/components/generateTopic'
import customTopic from '@/components/customTopic'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',name: 'generateTopic',component: generateTopic},
    {path: '/generateTopic',name: 'generateTopic',component: generateTopic},
    {path: '/customTopic',name: 'customTopic',component: customTopic}
  ]
})



