<template>
  <div class="hello">
    <div>
      <el-row>
        <el-col>
          <el-card class="box-card">
            <el-input type="textarea" v-model="words"> </el-input>
            <span class="typer-cursor">||</span>
          </el-card>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <div>
            <el-card class="box-card">
              <div class="words" style="white-space:pre-wrap width: 300px">{{ words }}</div>
              
            </el-card>
          </div>
        </el-col>
      </el-row>
      <el-container>
        <div>
          <el-button @click="printStart">测试 </el-button>
        </div>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "printText",
  props: ["text"],
  watch: {
    text(newVal, oldVal) {
      console.log(newVal, oldVal, this.text);
    },
  },
  data() {
    return {
      word: [], //整句
      words: [],
      letters: [], //打撒
    };
  },
  methods: {
    test() {
      console.log(this.text);
    },

    printStart() {
      this.letters = this.text.split("");
      for (let i = 0; i < this.letters.length; i++) {
        let this1 = this;
        setTimeout(
          function (lt, i) {
            this1.word.push(lt[i]);
            this1.words = this1.word.join("");
            console.log(this1.words);
            console.log(i);
          },
          20 * i,
          this.letters,
          i
        );
      }
    },
  },
  mounted() {},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.typer-cursor {
  height: 100%;
  width: 3px;
  background: #f9101099;
  color: #f9101099;
  animation: flash 1.5s linear infinite;
}
</style>



